@font-face {
  font-family: "Gilroy";
  font-style: normal;
  font-weight: 400;
  src: url(../fonts/gilroy-regular.ttf);
}
@font-face {
  font-family: "Gilroy";
  font-style: normal;
  font-weight: 700;
  src: url(../fonts/gilroy-bold.ttf);
}
@font-face {
  font-family: "Gilroy";
  font-style: normal;
  font-weight: 500;
  src: url(../fonts/gilroy-medium.ttf);
}
@font-face {
  font-family: "Gilroy";
  font-style: normal;
  font-weight: 600;
  src: url(../fonts/gilroy-semibold.ttf);
}
@font-face {
  font-family: "Gilroy";
  font-style: normal;
  font-weight: 800;
  src: url(../fonts/Gilroy-ExtraBold.otf);
}
* {
  font-family: "Gilroy", "Arial", sans-serif;
  font-style: normal;
}

* {
  margin: 0;
  padding: 0;
}

* {
  box-sizing: content-box;
}

*::before,
*::after {
  box-sizing: inherit;
}

ul li {
  list-style: none;
}

a {
  text-decoration: none;
}

.wrapper {
  background-image: url(../../img/index/wrapper.png);
  background-position: center;
  background-repeat: no-repeat;
  width: 100%;
  height: auto;
}

.header {
  width: 100%;
}

.header-wrapper {
  max-width: 1200px;
  display: flex;
  align-items: center;
  padding-top: 24px;
  padding-left: 215px;
}

.menu-headr__list {
  display: flex;
  padding-left: 0;
}

.menu-header__link {
  font-weight: bold;
  font-size: 16px;
  line-height: 19px;
  color: #2C2F36;
  text-decoration: none;
  padding-right: 72px;
  position: relative;
  content: "";
}

.menu-header__link:hover {
  color: #FB8C00;
}

.menu-header__link:active,
.menu-header__link:focus {
  color: #cf7404;
}

.menu__header-link--active:hover:before {
  position: absolute;
  content: "";
  width: 81px;
  height: 3px;
  background-color: #FB8C00;
  bottom: -5px;
  left: 0;
}

.button__print {
  font-weight: bold;
  font-size: 16px;
  line-height: 140%;
  color: #FFFFFF;
  background: #FB8C00;
  border-radius: 88px;
  padding-left: 58px;
  padding-right: 30px;
  padding-top: 13px;
  padding-bottom: 13px;
  border: none;
  position: relative;
  content: "";
}

.button__print:hover {
  background-color: #2F80ED;
}

.button__print:active,
.button__print:focus {
  background-color: #0649a0;
}

.button__print::before {
  position: absolute;
  content: "";
  background-image: url(../../img/index/print.svg);
  width: 12px;
  height: 13px;
  left: 32px;
  bottom: 17px;
}

.header__logo {
  padding-right: 314px;
}

.about {
  width: 100%;
  padding-bottom: 300px;
}

.about-wrapper {
  max-width: 1600px;
  margin: 0 auto;
  padding-left: 200px;
  display: flex;
}

.about-title {
  font-weight: 800;
  font-size: 72px;
  line-height: 88px;
  color: #2C2F36;
  max-width: 370px;
  padding-top: 109px;
  margin-bottom: 199px;
  position: relative;
  content: "";
}

.about-title::after {
  position: absolute;
  content: "";
  width: 72px;
  height: 3px;
  background-color: #FB8C00;
  bottom: -32px;
  left: 0;
}

span {
  color: #FB8C00;
}

.about-list {
  display: flex;
  margin: 0;
  list-style: none;
}
.about-list__item:first-child {
  padding-left: 0;
  max-width: 162px;
}
.about-list__item:nth-child(2) {
  max-width: 195px;
}
.about-list__item:last-child {
  max-width: 120px;
}

.about-list__text {
  font-weight: 500;
  font-size: 16px;
  line-height: 140%;
  color: #2C2F36;
  position: relative;
}

.about-svg__first::before,
.about-img__second::before,
.about-svg__third::before {
  position: absolute;
  content: "";
  background-repeat: no-repeat;
  background-position: center;
  width: 26px;
  height: 28px;
  top: -68px;
  left: 16px;
}

.about-svg__first::before {
  background-image: url(../../img/index/aboutf.svg);
}

.about-img__second::before {
  background-image: url(../../img/index/abouts.svg);
}

.about-svg__third::before {
  background-image: url(../../img/index/aboutt.svg);
}

.about-list__item {
  padding-left: 63px;
  position: relative;
  contain: "";
}

.about-list__item::before {
  position: absolute;
  content: "";
  background-color: #FFFFFF;
  width: 60px;
  height: 60px;
  border-radius: 50px;
  top: -84px;
}

.aboout-list__line {
  position: relative;
  content: "";
}

.aboout-list__line::after {
  position: absolute;
  content: "";
  width: 3px;
  height: 150px;
  background-color: #9BA9B5;
  opacity: 0.2;
  border-radius: 36px;
  right: -30px;
  bottom: 0;
}

.about-picture__img {
  padding-top: 80px;
}

.catalog {
  width: 1170px;
  margin: 0 auto;
  padding-top: 73px;
  padding-bottom: 194px;
}

.title {
  font-weight: 800;
  font-size: 48px;
  line-height: 59px;
  color: #2C2F36;
}

.catalog__title {
  padding-bottom: 148px;
}

.gallery {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 126px 30px;
}

.products {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end;
  background-color: #ebf6ff;
  background-position: center;
  background-repeat: no-repeat;
  border-radius: 35px;
  max-width: 370px;
  max-height: 244px;
  padding-bottom: 40px;
}

.products__subtitle {
  font-weight: bold;
  font-size: 18px;
  line-height: 140%;
  color: #2C2F36;
  text-align: center;
}

.products__text {
  font-weight: 500;
  font-size: 16px;
  line-height: 140%;
  color: #2C2F36;
  width: 228px;
  height: 44px;
  text-align: center;
  padding-top: 16px;
}

.products:nth-of-type(1) > .products__subtitle {
  max-width: 215px;
}

.products:nth-of-type(7) > .products__subtitle {
  max-width: 280px;
}

.products:nth-of-type(8) > .products__text {
  width: 262px;
}

.products__img {
  display: block;
}

.products:nth-of-type(1) > .products__img {
  transform: translate(0, -33px);
}

.products:nth-of-type(2) > .products__img {
  transform: translate(0, -48px);
}

.products:nth-of-type(3) > .products__img {
  transform: translate(0, -55px);
}

.products:nth-of-type(4) > .products__img {
  transform: translate(0, -32px);
}

.products:nth-of-type(5) > .products__img {
  transform: translate(0, -54px);
}

.products:nth-of-type(6) > .products__img {
  transform: translate(0, -40px);
}

.products:nth-of-type(7) > .products__img {
  transform: translate(0, -45px);
}

.products:nth-of-type(8) > .products__img {
  transform: translate(0, -47px);
}

.products:nth-of-type(9) > .products__img {
  transform: translate(0, -45px);
}

.work {
  width: 100%;
  padding-bottom: 350px;
}

.work-wrapper {
  max-width: 1387px;
}

.work-container {
  display: flex;
  justify-content: space-between;
  padding-top: 64px;
}

.work__title {
  padding-left: 215px;
}

.work-list {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr;
}

.work-list__text {
  font-weight: bold;
  font-size: 18px;
  line-height: 140%;
  color: #2C2F36;
  max-width: 128px;
  padding-left: 116px;
  display: table-cell;
  vertical-align: middle;
}

.work-list__item {
  box-shadow: 0px 4px 68px rgba(32, 52, 91, 0.08);
  border-radius: 35px;
  width: 271px;
  height: 151px;
  position: relative;
  content: "";
  overflow: hidden;
  display: table;
}

.work-list__item::before {
  position: absolute;
  content: "";
  background-image: url(../../img/index/Ellipse.svg);
  background-repeat: no-repeat;
  width: 168px;
  height: 168px;
  top: -59px;
  left: -68px;
  overflow: hidden;
}

.work-list__item:nth-child(1) {
  grid-column: 1/3;
  box-shadow: 0px 4px 68px rgba(32, 52, 91, 0.08);
  border-radius: 35px;
  width: 570px;
  height: 151px;
}

.work-list__item:nth-child(1) > .work-list__text {
  max-width: 378px;
  padding-left: 137px;
}

.price {
  width: 100%;
}

.price-wrapper {
  max-width: 1140px;
  margin-left: 215px;
  padding-bottom: 184px;
}

.parameters-block {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr;
}

.price__title {
  padding-bottom: 54px;
}

.form-contents {
  width: 670px;
  height: 635px;
  background-color: white;
  box-shadow: 0px 4px 68px rgba(33, 33, 33, 0.06);
  border-radius: 35px;
  margin-left: 30px;
}

.parameters {
  background-color: #f1f5f8;
  border-radius: 35px;
  width: 470px;
  min-height: 635px;
}

.parameters-link {
  padding-top: 44px;
  padding-left: 40px;
  padding-bottom: 48px;
}

.parameters-link__item {
  padding-right: 32px;
  font-weight: bold;
  font-size: 18px;
  line-height: 140%;
  color: #2C2F36;
  opacity: 0.4;
  position: relative;
  content: "";
}

.parameters-link__item:nth-child(2) {
  padding-right: 0;
}

.parameters-link__item:hover {
  font-weight: bold;
  font-size: 18px;
  line-height: 140%;
  color: #FB8C00;
  opacity: 1;
}

.parameters-link__item:hover::before,
.parameters__link:hover::before {
  position: absolute;
  content: "";
  height: 3px;
  background-color: #FB8C00;
  left: 0;
  bottom: -5px;
}

.parameters-link__item:hover::before {
  width: 101px;
}

.parameters__link:hover::before {
  width: 87px;
}

.parameters__list {
  padding-left: 40px;
}

.parameters__list-item {
  padding-bottom: 28px;
}

.parameters__list-paragraph {
  font-weight: 500;
  font-size: 14px;
  line-height: 140%;
  color: #2C2F36;
  padding-top: 16px;
  width: 388px;
  position: relative;
  content: "";
}

.parameters__list-subtext {
  position: relative;
  content: "";
  font-weight: 600;
  font-size: 16px;
  line-height: 140%;
  color: #2C2F36;
}

.parameters__list-subtext {
  display: block;
}

.parameters__list-subtext::-webkit-details-marker {
  display: none;
}

.parameters__list-subtext::after {
  position: absolute;
  content: "+";
  font-size: 20px;
  right: 47px;
}

.parameters__list-text[open] .parameters__list-subtext::after {
  content: "-";
  color: #2f80ed;
  font-size: 20px;
  right: 47px;
}

.parameters__list-text[open] .parameters__list-subtext {
  font-weight: 600;
  font-size: 16px;
  line-height: 140%;
  color: #2F80ED;
}

.parameters__list-paragraph::after {
  position: absolute;
  content: "";
  background-color: #2f80ed;
  width: 390px;
  height: 3px;
  bottom: -16px;
  left: 0;
}

